<template>
  <div class="task-item-wrap" @click="handleExpand">
    <div id="header">
      <div class="name">{{ dataSource.title }}</div>
      <div class="info">
        <div class="avatar" translate="no">
          <img :src="dataSource.logo || defaultImg" alt="" />
        </div>
        <div class="name" translate="no">{{ dataSource.author }}</div>
      </div>
    </div>
    <div class="no-expand" v-if="!isExpand">
      <div class="reward">
        {{ $t('admin.reward')
        }}<span translate="no"
          >{{ $gbUtils.formatBalance(dataSource.award) }}{{ tokenName }}</span
        >
      </div>
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#a-starbeifen2"></use>
      </svg>
      <div class="reward">
        {{ $t('admin.schedule')
        }}<span translate="no"
          >{{ dataSource.completionsTimes }}/{{ dataSource.times }}</span
        >
      </div>
    </div>
    <div v-else>
      <div id="task-info">
        <div class="info-wrap">
          <div class="item">
            <div class="name">{{ $t('admin.reward') }}</div>
            <div class="value" translate="no">
              {{ $gbUtils.formatBalance(dataSource.award) }}{{ tokenName }}
            </div>
          </div>
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#a-starbeifen2"></use>
          </svg>
          <div class="item item1">
            <div class="name">{{ $t('admin.schedule') }}</div>
            <div class="value" translate="no">
              {{ dataSource.completionsTimes }}/{{ dataSource.times }}
            </div>
          </div>
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#a-starbeifen2"></use>
          </svg>
          <div class="item">
            <div class="name">{{ $t('admin.validTime') }}</div>
            <div class="value" translate="no">
              {{ getTime(validTime) }}{{ getUnit(validTime) }}
            </div>
          </div>
        </div>
        <div class="info-wrap">
          <div class="item">
            <div class="name">{{ $t('common.submit') }}</div>
            <div class="value" translate="no">
              {{ taskInfo.submitQuantity || 0 }}
            </div>
          </div>
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#a-starbeifen2"></use>
          </svg>
          <div class="item item1">
            <div class="name">{{ $t('admin.review') }}</div>
            <div class="value" translate="no">
              {{ taskInfo.checkQuantity || 0 }}
            </div>
          </div>
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#a-starbeifen2"></use>
          </svg>
          <div class="item">
            <div class="name">{{ $t('admin.pass') }}</div>
            <div class="value" translate="no">
              {{ dataSource.completionsTimes || 0 }}
            </div>
          </div>
        </div>
      </div>
      <div id="task-detail">
        <div class="name">{{ $t('admin.detail') }}：</div>
        <div class="des">
          {{ dataSource.content }}
           <svg
            class="icon"
            aria-hidden="true"
            @click.stop="handleCopy(dataSource.content)"
          >
            <use xlink:href="#fuzhi"></use>
          </svg>
        </div>
      </div>
      <div id="button-group">
        <template v-if="type === 0">
          <template>
            <VanButton color="#f8bb5a" class="btn" @click.stop="handleOpenLink">
              {{ dataSource.buttonName }}
            </VanButton>
            <VanButton
              color="#00c873"
              class="btn upload"
              @click.stop="handleShowUpload"
              :disabled="myInfo.type > 1"
            >
              {{ $t('admin.finishTask') }}
            </VanButton>
          </template>
        </template>
        <template v-else>
          <VanButton color="#f8bb5a" class="btn" @click.stop="handleOpenLink" translate="no">
            {{ dataSource.buttonName }}
          </VanButton>
          <VanButton
            color="#00c873"
            class="btn reject"
            v-if="dataSource.userStatus === 1"
          >
            {{ $t('admin.passed') }}
          </VanButton>
          <VanButton
            color="#7e6e53"
            class="btn reject"
            v-if="dataSource.userStatus === 2"
            @click.stop="handleShowReject"
          >
            {{ $t('admin.reviewFail') }}
          </VanButton>
          <template v-if="dataSource.userStatus === -1">
            <VanButton color="#f8bb5a" class="btn" @click.stop="handleOpenLink">
              {{ dataSource.buttonName }}
            </VanButton>
            <VanButton
              color="#00c873"
              class="btn upload"
              v-if="dataSource.userStatus === -1"
              @click.stop="handleShowUpload"
              :disabled="myInfo.type > 1"
            >
              {{ $t('admin.finishTask') }}
            </VanButton>
          </template>
          <VanButton
            color="#7e6e53"
            class="btn ing"
            disabled
            v-else-if="dataSource.userStatus === 0"
          >
            <div translate="no">
              <van-count-down :time="auditTime * 1000" />
            </div>
            <div>{{ $t('admin.reviewing') }}</div>
          </VanButton>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { handleTaskDetail } from '@/services/task'
export default {
  name: 'TaskItem',
  props: ['dataSource', 'index', 'type'],
  data () {
    return {
      tokenName: '',
      validTime: null,
      auditTime: null,
      isExpand: false,
      taskInfo: {},
      defaultImg: require('../../../assets/logo.png')
    }
  },
  computed: {
    ...mapState(['myInfo'])
  },
  methods: {
    async handleExpand () {
      this.isExpand = !this.isExpand
      // console.log('this.dataSource', this.dataSource)
      if (this.isExpand) {
        const id = this.type === 0 ? this.dataSource.id : this.dataSource.taskId

        const resp = await handleTaskDetail(id)
        if (!resp.success) return
        this.taskInfo = resp.data
      }
    },
    handleOpenLink () {
      window.open(this.dataSource.url, '_blank')
    },
    handleShowUpload () {
      if (this.myInfo.type > 1) return
      this.$emit('show', this.dataSource)
    },
    handleShowReject () {
      this.$emit('reject', this.index)
    },
    // 计算分钟,小时,天
    getTime (value) {
      if (+value >= 86400) {
        return (+value / 86400).toFixed(0)
      } else if (+value >= 3600) {
        return (+value / 3600).toFixed(0)
      } else if (+value >= 60) {
        return (+value / 60).toFixed(0)
      }
    },
    // 获取天小时单位
    getUnit (value) {
      if (+value >= 86400) {
        return this.$t('common.day')
      } else if (+value >= 3600) {
        return this.$t('common.hour')
      } else if (+value >= 60) {
        return this.$t('common.min')
      } else {
        return this.$t('common.second')
      }
    },
    handleCopy (content) {
      const text = document.createElement('textarea')
      text.value = content
      document.body.appendChild(text)
      text.select() // 选择对象
      if (document.execCommand('copy')) {
        document.execCommand('copy')
        this.$toast('copied')
      }
      // 执行浏览器复制命令
      document.body.removeChild(text)
    }
  },
  mounted () {
    // console.log('this.myInfo', this.myInfo)
    this.tokenName = JSON.parse(localStorage.getItem('tokenList'))[this.dataSource.tokenType]
    const expirationTime = this.dataSource.expirationTime.replace(new RegExp('-', 'gm'), '/')
    this.validTime = (new Date(expirationTime).getTime() - new Date().getTime()) / 1000
    if (this.dataSource.userStatus === 0) {
      const auditExpirationTime = this.dataSource.auditExpirationTime.replace(new RegExp('-', 'gm'), '/')
      this.auditTime = (new Date(auditExpirationTime).getTime() - new Date().getTime()) / 1000
    }
  }
}
</script>

