<template>
  <VanPopup
    style="
      box-sizing: border-box;
      width: 93%;
      max-width: 400px;
      background-color: #1d1d1d;
      padding: 20px 30px;
      box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 1);
      border-radius: 4px;
    "
    round
    @click-overlay="closePop"
    v-model="show"
  >
    <div class="upload-wrap">
      <div class="title">{{ $t('admin.finishTask') }}</div>

      <div id="image-info">
        <div class="name">{{ $t('admin.taskImg') }}</div>
        <div class="set-image">
          <van-uploader
            :after-read="afterRead"
            v-model="fileList"
            :max-size="10240 * 1024"
            @oversize="onOversize"
          >
            <div class="upload"><van-icon name="plus" size="25" /></div>
          </van-uploader>
        </div>
      </div>
      <div id="text-info">
        <div class="name">{{ $t('admin.taskFinishDes') }}</div>
        <van-field
          translate="no"
          class="textarea"
          v-model="content"
          :placeholder="$t('common.inputContent')"
          maxlength="200"
          rows="4"
          type="textarea"
        />
      </div>
      <div class="button" @click="handleConfirm">
        {{ $t('common.confirm') }}
      </div>
    </div>
  </VanPopup>
</template>

<script>
import config from '@/config/app.config'
import { handleUpload } from '@/services/task'
import { upload } from '@/services/upload'
export default {
  name: 'informationDetail',
  props: ['taskId'],
  data () {
    return {
      show: true,
      fileList: [],
      content: '',
      images: ''
    }
  },
  methods: {
    closePop () {
      this.$emit('close')
    },
    onOversize (file) {
      console.log(file)
      this.$toast(this.$t('common.10m'))
    },
    // 图片上传
    async afterRead (file) {
      // 此时可以自行将文件上传至服务器
      var formdata = new FormData()
      formdata.append('file', file.file)
      const resp = await upload(formdata)
      if (!resp.success) {
        this.fileList = []
        return
      }
      this.images = config.imgUrl + resp.data.url
    },
    async handleConfirm () {
      if (!this.images || !this.content) return this.$toast(this.$t('common.fill'))
      const resp = await handleUpload({
        taskId: this.taskId,
        content: this.content,
        images: this.images
      })
      console.log('resp', resp)
      if (!resp.success) return
      this.$toast('success')
      this.$emit('success')
      this.$parent.getList()
    }
  },
  mounted () {
    console.log(this.taskId)
  }
}
</script>

